*,
*::before,
*::after {
  box-sizing: border-box; }

.range {
  box-sizing: border-box;
  font-size: 16px;
  position: fixed;
  top: -1.2em;
  height: 2em;
  background-color: transparent;
  cursor: pointer;
  -webkit-appearance: none;
  width: 100%; }
  .range::-webkit-slider-thumb {
    -webkit-appearance: none; }
  .range:focus {
    outline: none; }
  .range::-ms-track {
    width: 100%;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent; }
  .range:invalid {
    box-shadow: none; }
  .range::-moz-submit-invalid {
    box-shadow: none; }
  .range::-moz-ui-invalid {
    box-shadow: none; }
  .range::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 2.5em;
    height: .8em;
    margin-top: .5em;
    background-color: #fff;
    border: 2px solid rgba(255, 255, 255, 0.5);
    cursor: pointer; }
  .range::-moz-range-thumb {
    width: 2.5em;
    height: .8em;
    margin-top: .5em;
    background-color: #fff;
    border: 2px solid rgba(255, 255, 255, 0.5);
    cursor: pointer; }
  .range::-ms-thumb {
    width: 2.5em;
    height: .8em;
    margin-top: .5em;
    background-color: #fff;
    border: 2px solid rgba(255, 255, 255, 0.5);
    cursor: pointer; }
  .range:hover::-webkit-slider-thumb {
    border-color: rgba(255, 255, 255, 0.7); }
  .range:hover::-moz-range-thumb {
    border-color: rgba(255, 255, 255, 0.7); }
  .range:hover::-ms-thumb {
    border-color: rgba(255, 255, 255, 0.7); }
  .range:active::-webkit-slider-thumb {
    border-color: white; }
  .range:active::-moz-range-thumb {
    border-color: white; }
  .range:active::-ms-thumb {
    border-color: white; }
  .range::-webkit-slider-runnable-track {
    width: 100%;
    cursor: pointer;
    height: 1em;
    border-bottom: 2px solid rgba(255, 255, 255, 0.5);
    background-color: transparent; }
  .range::-moz-range-track {
    width: 100%;
    cursor: pointer;
    height: 1em;
    border-bottom: 2px solid rgba(255, 255, 255, 0.5);
    background-color: transparent; }
  .range::-ms-track {
    background: transparent;
    border-color: transparent;
    color: transparent; }

